.loading {
  animation: rotate  1.3s infinite linear; }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.btn {
  display: inline-flex;
  align-items: center;
  height: 40px;
  padding-right: 16px;
  padding-left: 16px;
  border-radius: 6px;
  background-color: #D8EEFF;
  color: #008EFF;
  font-size: 14px;
  line-height: 1.4;
  cursor: pointer; }
  .btn:disabled {
    cursor: not-allowed;
    background-color: #cfd8dc;
    color: white; }

.btn--blue {
  color: white;
  background-color: #008EFF; }

.btn--large {
  height: 40px;
  padding-right: 20px;
  padding-left: 20px;
  font-size: 16px;
  line-height: 1.5; }
  @media (min-width: 768px) {
    .btn--large {
      height: 48px; } }

.btn--grey {
  background-color: #36474f; }

.btn--light-grey {
  background-color: #8fa4af; }

.btn--small {
  height: 24px;
  font-size: 12px;
  line-height: 1.3; }

.btn--red {
  background-color: #f96c97; }

.btn--link {
  color: #445a65;
  font-size: 14px;
  font-weight: 700;
  line-height: 20px; }

.btn--loading {
  position: relative; }
  .btn--loading .btn__loader {
    display: none; }
  .btn--loading:disabled .btn__txt {
    visibility: hidden; }
  .btn--loading:disabled .btn__loader {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 24px;
    height: 24px;
    margin-top: -12px;
    margin-left: -12px;
    animation: rotate  1.3s infinite linear; }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

