.steps {
  display: flex;
  margin-top: 0;
  padding-left: 0;
  counter-reset: step;
  justify-content: center;
  margin-bottom: 24px; }

.step {
  position: relative;
  width: 100%;
  list-style-type: none;
  font-size: 12px;
  line-height: 1.33;
  text-align: center; }
  @media (min-width: 768px) {
    .step {
      width: 33.33%; } }
  .step:before {
    content: counter(step);
    display: block;
    width: 20px;
    height: 20px;
    margin-right: auto;
    margin-bottom: 4px;
    margin-left: auto;
    border: 1px solid #8fa4af;
    border-radius: 50%;
    background-color: #eceff1;
    font-size: 10px;
    line-height: 20px;
    text-align: center;
    counter-increment: step; }
  .step:after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 11px;
    left: -50%;
    width: 100%;
    height: 1px;
    background-color: #8fa4af; }
  .step:first-child:after {
    content: none; }

.step--active:before {
  border-color: #29CB98;
  background-color: #29CB98;
  color: white; }

.step--active + .step:after {
  background-color: #52a3ff; }

.step--complete + .step:after {
  background-color: #36474f; }

.step--complete {
  color: #768492; }
  .step--complete::before {
    border-color: #29CB98;
    background-color: #eceff1;
    color: #768492; }
  .step--complete + .step:after {
    background-color: #29CB98; }

.step--next-disabled + .step {
  color: #bec8d1; }
  .step--next-disabled + .step:after {
    background-color: #e8eef1; }
  .step--next-disabled + .step:before {
    background-color: #e8eef1;
    color: #bec8d1;
    border-color: #e8eef1; }

